import './App.css';
function App() {
  return (
    <div className="App">
          <h1>Simple React WebSite to generate Random Number</h1>
      <h3>How many Number</h3>
      <input id="start" type="number" name="start" placeholder="Number"></input>
      <button onClick={() => {
        var start = parseInt(document.getElementById("start").value);
        let listnumber = "";
        let i;
        for (i = 0; i < start; ++i){
          listnumber = listnumber + " " + Math.ceil(Math.random()*start) ;
        }
        document.getElementById("list").innerHTML = listnumber;
      }}>Generate</button>
      <br></br>
      <span id="list"></span>
    </div>
  );
}

export default App;
